    computed: {
      checkboxErrors () {
        const errors = []
        if (!this.$v.checkbox.$dirty) return errors
        !this.$v.checkbox.checked && errors.push('You must agree to continue!')
        return errors
      },
      orderTypeErrors () {
        const errors = []
        if (!this.$v.orderType.$dirty) return errors
        !this.$v.orderType.required && errors.push('Order type is required')
        return errors
      },
      
      telErrors () {
        const errors = []
        if (!this.$v.tel.$dirty) return errors
        // !this.$v.name.maxLength && errors.push('Name must be at most 10 characters long')
        !this.$v.tel.required && errors.push('Tel is required.')
        return errors
      },

      // nameErrors () {
      //   const errors = []
      //   if (!this.$v.name.$dirty) return errors
      //   !this.$v.name.maxLength && errors.push('Name must be at most 10 characters long')
      //   !this.$v.name.required && errors.push('Name is required.')
      //   return errors
      // },

      orderDetailErrors () {
        const errors = []
        if (!this.$v.orderDetail.$dirty) return errors
        !this.$v.orderDetail.required && errors.push('orderDetail is required.')
        return errors
      },
      dateErrors () {
        const errors = []
        if (!this.$v.date.$dirty) return errors
        !this.$v.date.required && errors.push('Date is required.')
        return errors
      },
     codeErrors () {
